// src/components/sections/StatsSection.tsx
'use client';

import { useEffect, useState } from 'react';
import { Users, TrendingUp, CheckCircle, Globe } from 'lucide-react';

interface Stat {
  value: string;
  label: string;
  suffix?: string;
  icon?: React.ReactNode;
}

interface StatsSectionProps {
  stats?: Stat[];
  animated?: boolean;
}

const StatsSection = ({ 
  stats = [
    { value: '50', label: 'Clients satisfaits', suffix: 'k+', icon: <Users /> },
    { value: '95', label: 'Qualit√© du travail', suffix: '%', icon: <CheckCircle /> },
    { value: '10', label: 'Projets complets', suffix: 'k+', icon: <TrendingUp /> },
    { value: '15', label: 'Pays desservis', suffix: '+', icon: <Globe /> }
  ],
  animated = true
}: StatsSectionProps) => {
  const [animatedValues, setAnimatedValues] = useState(stats.map(() => '0'));

  useEffect(() => {
    if (animated) {
      const timers = stats.map((stat, index) => {
        const targetValue = parseInt(stat.value.replace(/[^0-9]/g, ''));
        let currentValue = 0;
        const increment = targetValue / 50; // 50 frames
        
        const timer = setInterval(() => {
          currentValue += increment;
          if (currentValue >= targetValue) {
            currentValue = targetValue;
            clearInterval(timer);
          }
          
          setAnimatedValues(prev => {
            const newValues = [...prev];
            newValues[index] = Math.floor(currentValue).toString();
            return newValues;
          });
        }, 30);
        
        return timer;
      });
      
      return () => timers.forEach(timer => clearInterval(timer));
    }
  }, [animated, stats]);

  return (
    <section className="py-20 bg-gradient-to-r from-primary-900 to-primary-700 text-white">
      <div className="container px-4 mx-auto">
        <div className="text-center mb-12">
          <h2 className="text-3xl font-bold mb-4">Notre impact en chiffres</h2>
          <p className="text-xl opacity-90 max-w-2xl mx-auto">
            Des r√©sultats concrets qui t√©moignent de notre expertise et de notre engagement
          </p>
        </div>
        
        <div className="grid grid-cols-2 md:grid-cols-4 gap-8">
          {stats.map((stat, index) => (
            <div key={index} className="text-center">
              <div className="inline-flex items-center justify-center w-16 h-16 rounded-full bg-white/10 mb-4">
                {stat.icon || <div className="text-2xl">üìä</div>}
              </div>
              
              <div className="text-4xl md:text-5xl font-bold mb-2">
                {animated ? animatedValues[index] : stat.value}
                {stat.suffix && <span>{stat.suffix}</span>}
              </div>
              
              <p className="text-lg opacity-90">{stat.label}</p>
            </div>
          ))}
        </div>
      </div>
    </section>
  );
};

export default StatsSection;